.hero{
    height: 100vh;
    background: linear-gradient(180deg, rgba(253,225,255,1) 0%, rgba(225,255,234,1) 60%);
    display: flex;
}

.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 180px;
    gap: 20px;
    line-height: 1.1;
}
.hero-left h2{
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}

.hero-left p{
    color: #171717;
    font-size: 60px;
    font-weight: 700;
}
.hand-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;
}
.hand-hand-icon img{
    width: 100px;
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-weight: 500;
    font-size: 22px;
}

.hero-right{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
}
.hero-right img{
    margin-block: auto;
    width: 532px;
    height: 620px;
}