.loginsignup{
    width: 100%;
    height: 800px;
    background: #99899b;
    padding-top: 80px;
}
.loginsignup-container{
    width: 580px;
    height: 600px;
    background: white;
    margin: auto;
    padding: 40px 60px;
    border-radius: 50px;
}
.loginsignup-container h1{
    margin: 20px 0px;
}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 30px;
}
.loginsignup-fields input{
    height: 70px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    border-radius: 30px;
    outline: none;
    font-size: 18px;
    color: #5c5c5c;
}
.loginsignup-container button{
    width: 100%;
    height: 72px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    cursor: pointer;
    border: none;
    font-size: 24px;
    font-weight: 500;
}
.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
.loginsignup-login span{
    text-decoration: underline;
    color: #ff4141;
    font-weight: 600;
    cursor: pointer;
}
.loginsignup-agree{
    display: flex;
    align-items: center;
    margin-top: 20px;
}
.loginsignup-agree p{
    margin-left: 5px;
    font-size: 16px;
    font-weight: 400;
}