.descriptionbox{
    margin: 120px 170px;
}
.navigator{
    display: flex;
}
.nav-box{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0;
}
.fade{
    background: #fbfbfb;
    color: #555;
}
.description{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #D0D0D0;
    padding: 48px;
    padding-bottom: 70px;
}